          </router-link>
        </v-col>

        <!-- <v-col cols="6" md="3" @click="logout" v-ripple>
            <v-alert outlined color="dark" border="left">
              <h2 class="">登出</h2>
              <v-img :src="exit_pic" aspect-ratio="2.5" contain></v-img>
            </v-alert>
        </v-col> -->
        
      </v-row>
    </v-container>
    <!-- <pre>
      {{this.$store.state.pData}}
    </pre> -->
